.like{
	display: flex;
    margin-left: auto;
	align-items: flex-end;
}
.image{
	position: relative;
	cursor: pointer;
	width: 20px;
	width: 20px;
	height: 20px;
	margin: 0px 0px 3px 5px;
}
.image img{
	position: absolute;width: 100%;height: 100%;top: 0;left: 0;
	object-fit: cover;
}